.section a {
  color: #fff;
}
.navbar {
  width: 100%;
  height: auto;
  background-color: white;
  padding: 20px;
  box-shadow: 0 1px 3px rgb(0 0 0 / 10%), 0 2px 2px rgb(0 0 0 / 6%), 0 0 2px rgb(0 0 0 / 7%);
  ul {
    display: flex;
    align-items: center;
  }
  li {
    margin: 0 15px;
    font-weight: bold;
  }
  a:hover {
    color: var(--color-primary);
  }
}
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-brand img {
  width: 50px;
  height: 50px;
}
.navbar-nav-left {
  text-transform: uppercase;
  flex: 1;
  margin-left: 20px;
}
.navbar-nav-right img {
  height: 20px;
  width: 20px;
  margin-right: 10px;
}
.navbar-nav-right {
  li:first-child a {
    display: flex;
    align-items: center;
  }
  li:nth-child(2) {
    margin: 0 5px;
  }
}
/* Footer & Social*/
.footer-conntainer {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 30px 30px;
}
.social {
  display: flex;
  align-items: center;
  margin: 20px 0;
}
.social a {
  margin-right: 20px;
}
.social img {
  width: 35px;
  height: 35px;
}
.divider {
  width: 95%;
  height: 1px;
  background-color: #ccc;
  margin: 40px auto;
}
/* Box */
.box {
  margin-bottom: 30px;
}
.box-inner {
  max-width: 700px;
  margin: 0 auto;
}
.box-text {
  max-width: 500px;
  text-align: center;
  justify-self: center;
  padding: 20px 0;
  margin: 0 auto;
}
.box-text .btn {
  margin-top: 20px;
}
.box-f .box-text {
  max-width: 600px;
}
/*Grid Style*/
.grid-col-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  align-items: center;
  justify-content: space-between;
}
.grid-reversed :first-child {
  order: 2;
}
/*Btn*/
.btn {
  cursor: pointer;
  display: inline-block;
  background: none;

  border: 1px #000 solid;
  border-radius: 50px;
  padding: 7px 16px;
  line-height: 1.2;
  text-align: center;
  text-decoration: none;
}
.btn-dark-outline {
  border-color: #000;
  color: #000;
}
.btn-dark-outline:hover,
.btn-light-outline:hover {
  background-color: rgb(0, 0, 0, 0.06);
}
.btn-light-outline {
  border-color: #fff;
  color: #fff;
}
.btn-dark {
  background-color: #000;
  color: #fff;
}
.btn-dark:hover {
  background-color: #333;
}
